    // z-index: 1;
}
  
  .rafflepress-tooltip[x-placement^="top"] {
    margin-bottom: 5px;
  }
  
  .rafflepress-tooltip[x-placement^="top"] .tooltip-arrow {
    border-width: 5px 5px 0 5px;
    border-left-color: transparent !important;
    border-right-color: transparent !important;
    border-bottom-color: transparent !important;
    bottom: -5px;
    left: calc(50% - 5px);
    margin-top: 0;
    margin-bottom: 0;
  }
  
  .rafflepress-tooltip[x-placement^="bottom"] {
    margin-top: 5px;
  }
  
  .rafflepress-tooltip[x-placement^="bottom"] .tooltip-arrow {
    border-width: 0 5px 5px 5px;
    border-left-color: transparent !important;
    border-right-color: transparent !important;
    border-top-color: transparent !important;
    top: -5px;
    left: calc(50% - 5px);
    margin-top: 0;
    margin-bottom: 0;
  }
  
  .rafflepress-tooltip[x-placement^="right"] {
    margin-left: 5px;
  }
  
  .rafflepress-tooltip[x-placement^="right"] .tooltip-arrow {
    border-width: 5px 5px 5px 0;
    border-left-color: transparent !important;
    border-top-color: transparent !important;
    border-bottom-color: transparent !important;
    left: -5px;
    top: calc(50% - 5px);
    margin-left: 0;
    margin-right: 0;
  }
  
  .rafflepress-tooltip[x-placement^="left"] {
    margin-right: 5px;
  }
  
